:root {
    --primary-color: #45ccb8;
    --light-gray: #2b3032;
    --border: #2b303218;
    --text-color: #ff686b;
    --roboto: "Roboto", sans-serif;
    --gugi: "Gugi", cursive;
    --sofia: "Sofia", cursive;
}


.main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 300px 300px;
}

a {
    text-decoration: none;
}

.primary-color {
    color: #45ccb8;
    color: var(--primary-color);
}

.bg-primary-color {
    background-color: #45ccb8 !important;
